@import '../../variables.scss';

.register-page {
  background-image: url('../../static/img/auth-bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;

  height: 700px;

  border-bottom: 1px solid $fontColorLight10;

  &__center {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 1200px;
    height: 100%;
    position: relative;

    padding-top: 30px;

    margin: 0 auto;
  }

  &__form {
    position: relative;
    flex-grow: 0;

    @media (max-width: 400px) {
      flex-grow: 1;
    }
  }
}
