@import '../../variables.scss';

.radio-buttons {
  display: flex;
}

.radio-button-item {
  display: flex;

  margin-right: 20px;

  cursor: pointer;

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }

  &__label {
    color: $fontColorLight50;

    text-transform: capitalize;
  }

  &__custom-radio {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;

    border: 1px solid $fontColorLight50;
    background: white;
    border-radius: 10px;
    margin-right: 10px;
  }

  &__custom-radio-inner {
    display: none;
    width: 12px;
    height: 12px;

    background: $mainColorGradient;
    border-radius: inherit;
  }

  &__input:focus + &__custom-radio {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__input:checked + &__custom-radio &__custom-radio-inner {
    display: block;
  }

  &__input:checked + &__custom-radio {
    border: 1px solid $mainColor;
  }

  &__input:checked + &__custom-radio + &__label {
    color: $fontColorLight75;
  }
}
