@import '../../variables.scss';

.toggle {
  display: flex;

  cursor: pointer;

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }

  &__toggle {
    position: relative;
    display: flex;
    align-items: center;
    width: 40px;
    height: 20px;

    border: 1px solid $fontColorLight25;
    border-radius: 10px;
    margin-right: 10px;
  }

  &__toggle-inner {
    position: relative;
    left: 4px;
    width: 12px;
    height: 12px;

    border-radius: inherit;

    background: $fontColorLight25;

    transition: left 0.1s ease-in-out;
  }

  &__input:focus + &__toggle {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }

  &__input:checked + &__toggle {
    border: 1px solid $mainColor;
  }

  &__input:checked + &__toggle > &__toggle-inner {
    left: 22px;

    background: $mainColorGradient;
  }
}
