@import '../../variables.scss';

.register-form {
  display: block;
  width: 380px;

  padding: 29px;
  padding-top: 43px;

  background-color: white;

  border: 1px solid rgba($color: #000, $alpha: 0.12);
  box-shadow: 0 10px 20px $fontColorLight5;
  border-radius: 4px;

  @media (max-width: 400px) {
    width: 100%;
  }

  &__title {
    display: block;
    position: relative;

    margin-bottom: 22px;

    color: $fontColor;

    font-family: $headerFont;
    font-size: 24px;
  }

  &__input-name,
  &__input-lastname,
  &__input-birthday,
  &__input-email,
  &__input-password {
    margin-bottom: 10px;
  }

  &__input-birthday {
    margin-bottom: 21px;
  }

  &__toggle {
    margin-bottom: 22px;
  }

  &__radio-buttons {
    margin-bottom: 20px;
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 28px;

    color: black;

    font-size: 14px;
    line-height: 24px;
  }

  &__login-link:focus {
    outline: auto;
    outline-width: 1px;
    outline-color: $mainColor;
  }
}
